*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.page_buttons{
    padding: 1vh 2vw;
    font-size: 1.5vw;
    line-height: 3vw;
    font-weight: bold;
    color:rgb(150,150,150);
    transition: all 0.5s ease;
}

.page_buttons:hover{
    color:white;
    text-shadow: 0px 0px 5px black;
}