#prob1cont{
    width: 80vw;
    height: 80vh;
    position: absolute;
    top: 10vw;
    left: 10vh;   
}
#prob1bar{
    width: 100%;
    display:flex;
    flex-direction: row;
    justify-content: center;

}
.prob1bars{
    font-weight: bolder;
    font-size: 3vh;
    line-height: 3vh;
    width: 10vw;
    text-align: center;
    transition : background 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.prob1bars:hover{
    background: rgba(200,200,200,0.4);
}

#mirror{
    width: 60vh;
    height: 60vh;
    border-radius: 50%;
    margin-top: 5vh;
    margin-left: 50%;
    transform: translateX(-50%);
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    border : 1vmin solid grey;

    overflow: hidden;
}

#mirror img{
    height: 60vh;
    margin-left: 50%;
    transform: translateX(-50%);
}