.robot {
    height: 72px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(-50%) translateX(-50%);
    transform-origin: center;
    position: absolute;
    top: 0;
    left: 0;
  }
  .robot * {
    box-sizing: border-box;
  }
  .robot .circle-bg {
    background: #cedbdd;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    position: relative;
  }
  .robot .circle-bg:after {
    content: "";
    position: absolute;
    z-index: 2;
    height: 100%;
    width: 100%;
    top: -0.33px;
    left: 0;
    border-radius: 100%;
  }
  .robot .circle-bg:hover .robot-face {
    top: 4px !important;
  }
  .robot .circle-bg:hover .eyes {
    top: 3px !important;
  }
  .robot .circle-bg:hover .eyes .eye_balls{
      margin-left: 0%;
  }
  .robot .circle-bg:hover .mouth {
    background: #195272;
    clip-path: polygon(0% 30%, 100% 30%, 100% 100%, 0% 100%);
  }
  .robot .circle-bg:hover .mouth:after {
    content: "";
    position: absolute;
    height: 80%;
    width: 80%;
    bottom: -30%;
    left: 50%;
    transform: translateX(-50%);
    background: #f9bbbb;
    border-radius: 100%;
  }
  .robot .circle-bg .robot-head {
    height: 24px;
    width: 24px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -2px;
    border-radius: 8.5px/6px;
    background: rgb(200,200,200);
    z-index: 4;
  }
  .robot .circle-bg .robot-head:after {
    content: "";
    position: absolute;
    top: -3px;
    height: 3px;
    width: 1px;
    left: 50%;
    transform: translateX(-50%);
    background: #195272;
  }
  .robot .circle-bg .robot-head:before {
    content: "";
    position: absolute;
    top: -6px;
    left: 50%;
    transform: translateX(-50%);
    height: 2px;
    width: 2px;
    background: #cedbdd;
    border-radius: 100%;
  }
  .robot .circle-bg .robot-head .robot-face {
    height: 14px;
    width: 19px;
    background: #cedbdd;
    position: absolute;
    top: 5.4px;
    left: 50%;
    transform: translateX(-50%);
    transition: all 200ms;
    border-radius: 5.4px;
  }
  .robot .circle-bg .robot-head .robot-face .eyes {
    height: 2px;
    width: 2px;
    background: white;
    border-radius: 100%;
    position: absolute;
    transition: all 200ms;
    top: 4px;
    overflow: hidden;
  }
  
  .eye_balls{
      background: rgb(37,37,37);
      width: 74%;
      height: 74%;
      border-radius: 50%;
      margin: 13%;
      transition: all 200ms ease;
  }
  .robot .circle-bg .robot-head .robot-face .eyes.left {
    left: 3px;
  }
  .robot .circle-bg .robot-head .robot-face .eyes.right {
    right: 3px;
  }
  .robot .circle-bg .robot-head .robot-face .mouth {
    height: 5.4px;
    width: 5.4px;
    border-radius: 100%;
    border: 1px solid transparent;
    border-bottom-color: #195272;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 5px;
    overflow: hidden;
  }
  .robot .circle-bg .robot-ear {
    position: absolute;
    height: 12px;
    width: 12px;
    border-radius: 100%;
    background: rgb(200,200,200);
    z-index: 3;
    top: 3px;
  }
  .robot .circle-bg .robot-ear.left {
    left: -2px;
  }
  .robot .circle-bg .robot-ear.right {
    right: -2px;
  }
  .robot .circle-bg .robot-body {
    height: 6px;
    width: 12px;
    background: rgb(200,200,200);
    position: absolute;
    bottom: -0.5px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 2.5px 2.5px 3px 3px;
  }