#space {
    width: 80vh;
    height: 80vh;
    background: grey;
    position: relative;
}

.goals {
    width: 50px;
    height: 50px;
    background: red;
    border-radius: 50%;
    line-height: 50px;
    text-align: center;
    position: absolute;
    transform: translateY(-50%) translateX(-50%);
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: bolder;
    color: white;
}

#robot_cont {
    width: 0;
    height: 0;
    position: relative;
    left: 0;
    top: 0;
    zindex: 1;
    transition: 0;

}

#current{
    position: absolute;
    top: 0;
    left: 50%;
    transform:translateX(-50%);
}