#items_cont{
    width: 80vh;
    height: 80vh;
    overflow: auto;
}
.item_cont{
    width:80vh;
    height: 50vh;
    display: flex;
    flex-direction: row;
    margin-top: 10vh;
    background: rgb(250,250,250);
}
.item_cont>div{
    padding: 5%;
}

.itemcont_left{
    width: 70%;
}

.itemcont_right{
    width: 30%;
    padding-top: 10vh; 
}

.product{
    height: 20vh;
    line-height: 20vh;
    font-size: 5vh;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color:rgb(100,100,100);
    font-weight: bolder;

}

.score{
    height: 10vh;
    line-height: 10vh;
}
.score strong{
    color: orangered;
}

.battery{
    height: 5vh;
    line-height: 5vh;
}

.speed{
    height: 5vh;
    line-height: 5vh;
}

.performence_title{
    height: 5vh;
    line-height: 5vh;
    background: rgb(70,70,70);
    color:white;
    text-align: center;
}

.performence{
    padding-left: 5%;
}